import Store from './Store';

export default class TimerFormStore extends Store {
  constructor() {
    super();

    this.modes = {
      work: { mode: ' ', minute: 0, second: 10 },
      share: { mode: ' ', minute: 0, second: 10 },
      rest: { mode: ' ', minute: 0, second: 10 },
    };
  }

  changeSetting({ name, minute, second }) {
    this.modes[name] = {
      ...this.modes[name],
      minute,
      second,
    };

    this.publish();
  }
}

export const timerFormStore = new TimerFormStore();
